<template>
  <div>
    <div class="banner" @click="handleBannerClick">
      <img class="banner-img" :src="bannerImg" alt="">

      <div class="banner-info">
        <div class="banner-title">
          {{this.sightName}}
        </div>
        <div class="banner-number">
          <span class="iconfont">&#xe626;</span>{{this.bannerImgs.length}}
        </div>
      </div>
    </div>
    <CommonGallary :imgs="bannerImgs"
                   @close="handleGallyClose"
                   v-show="showGallary"></CommonGallary>
  </div>
</template>

<script>
    import  CommonGallary from 'common/gallary/Gallary'
    export default {
        name: "Banner",
        props:{
          sightName:String,
          bannerImg:String,
          bannerImgs:Array
        },
        components:{
          CommonGallary
        },
        methods:{
          handleBannerClick(){
            this.showGallary = true
          },
          handleGallyClose(){
            this.showGallary = false
          }

        },

        data(){
          return {
            showGallary:false,
            }
        }
    }
</script>

<style scoped lang="stylus">
  .banner
    position:relative
    overflow: hidden
    height: 0
    padding-bottom: 55%
    .banner-img
      width:100%
    .banner-info
      display: flex
      position: absolute
      left:0
      right:0
      bottom:0
      line-height: .6rem
      color: #fff
      background-image: linear-gradient(top,rgba(0,0,0,0),rgba(0,0,0,.8))
      .banner-title
        font-size: .32rem
        padding: 0 .2rem
        flex:1
      .banner-number
        padding: 0 .4rem
        height .4rem
        margin-top: .14rem
        line-height: .4rem
        border-radius: .2rem
        background: rgba(0,0,0,.8)
        font-size: .24rem
        .banner-icon
          font-size: .24rem
</style>



