<template>
    <div class="icons">
      <swiper :options="swiperOption">
        <swiper-slide v-for="(page,index) of pages" :key="index">
          <div class="icon" v-for="item of page" :key="item.id">
            <div class="icon-img">
              <img class="icon-img-content" :src="item.imgUrl" alt="">
            </div>
            <p class="icon-desc">{{item.desc}}</p>
          </div>
        </swiper-slide>
      </swiper>
    </div>
</template>

<script>
    export default {
        name: "Icons",
        data(){
          return {
            swiperOption : {
              autoplay: false
            }
          }
        },
         props:{
            list: Array
         },
        computed:{

          pages(){
            const pages = []
            this.list.forEach((item,index) => {
              const page = Math.floor(index/8)//下标整除8 为显示页面
              if(!pages[page]){
                pages[page] = []
              }
              pages[page].push(item)
            })
            return pages
          }
        }

    }
</script>

<style scoped lang="stylus">
  @import '~styles/mixins.styl'
  .icons
    margin-top: .1rem
    overflow: hidden
    height: 0
    padding-bottom: 50%
    .icon
      position: relative
      overflow: hidden
      float:left
      width: 25%
      height: 0
      padding-bottom: 25%
      .icon-img
        position: absolute
        top: 0
        left: 0
        right: 0
        bottom:.44rem
        box-sizing: border-box
        padding: .1rem
        .icon-img-content
          height: 100%
          display: block
          margin: 0 auto
      .icon-desc
        position: absolute
        bottom: 0
        right: 0
        left: 0
        line-height: .44rem
        height: .44rem
        color: #333
        text-align: center
        ellipsis()

</style>



