<template>
 <div class="wrapper">
   <swiper :options="swiperOption" v-if="showSwipr">
     <!-- slides -->
     <swiper-slide v-for="item of swiperlist" :key="item.id">
       <img class="swiper-img" :src="item.imgUrl" >
     </swiper-slide>
     <div class="swiper-pagination"  slot="pagination"></div>

   </swiper>
 </div>
</template>

<script>
    export default {
        name: "homeswiper",
        props:{
          swiperlist: Array
        },
        data() {
          return {
            swiperOption: {
              pagination: '.swiper-pagination',
              loop: true,
              autoplay: 3000,
            }
          }
        },
        computed:{
          showSwipr(){
            return this.swiperlist.length
          }
        }
    }
</script>

<style scoped lang="stylus">
  .wrapper >>> .swiper-pagination-bullet-active
    background: #fff
  .wrapper
    voerflow: hidden
    width: 100%
    height:0
    background:#fff
    padding-bottom :31.25%
    .swiper-img {
      width: 100%
    }
</style>



