<template>
    <div>
      <div class="search">
        <input v-model="keyword" type="text" placeholder="输入城市名或拼音" class="search-input">
      </div>
      <div class="search-content" ref="search" v-show="keyword">
        <ul>
          <li v-for="item in list" class="search-item border-bottom"
              :key="item.id"
              @click="handleCityClick(item.name)"
          >{{item.name}}</li>
          <li class="search-item border-bottom" v-show="hasNoData">
            没有找到匹配数据
          </li>
        </ul>
      </div>
    </div>
</template>

<script>
    import Bscroll from 'better-scroll'
    import {mapMutations} from  'vuex'
    export default {
        name: "Search",
        props:{
          cities:Object
        },
        methods:{
          handleCityClick(city){
            // this.$store.commit("changeCity",city)
            this.changeCity(city)
            this.$router.push("/")
          },
          ...mapMutations(["changeCity"])
        },

        data(){
          return {
            keyword:'',
            list:[],
            timer:null
          }
        },
        computed:{
          hasNoData(){
            return !this.list.length
          }
        },
        mounted(){
            this.scroll = new Bscroll(this.$refs.search)
        },
      watch:{
          keyword(){
            if(this.timer){
              clearTimeout(this.timer)
            }
            if(!this.keyword){
              this.list = []
              return
            }
            this.timer = setTimeout(()=>{
              const result = []
              for(let i in this.cities){
                this.cities[i].forEach((value)=>{
                  if(value.spell.indexOf(this.keyword)>-1 || value.name.indexOf(this.keyword)> -1){
                    result.push(value)
                  }
                })
              }
              this.list = result
            },100)
          }
      }
    }
</script>

<style scoped lang="stylus">
  @import "~styles/varibles.styl"
  .search
    height: .72rem
    padding: .1rem
    background: $bgColor
    .search-input
      width:100%
      height: .62rem
      line-height: .62rem
      text-align:center
      border-radius:.06rem
      color:#666
      box-sizing:border-box
  .search-content
    overflow: hidden
    position: absolute
    top:1.68rem
    left:0
    right:0
    bottom:0
    z-index:100
    background:#eee
    .search-item
      line-height: .62rem
      padding-left: .2rem
      background:#fff
      color:#666
</style>



