<template>
    <div class="header">
      <div class="header-left">
        <div class="iconfont back-icon">&#xe624;</div>
      </div>
      <div class="header-input">
        <span class="iconfont">&#xe632;</span>
        输入城市/景点/游玩主题
      </div>
      <router-link to="/city">
        <div class="header-right">
          {{this.city}}
          <span class="iconfont arrow-iocn">&#xe64a;</span>
        </div>
      </router-link>
    </div>
</template>

<script>
  import  {mapState} from 'vuex'
    export default {
        name: "Header",
      computed:{
        ...mapState(['city'])
      }
    }
</script>

<style scoped lang="stylus">
  @import "~styles/varibles.styl"
  .header
    display: flex
    line-height: .86rem
    background: $bgColor
    color: #fff
    .header-left
      width: .64rem
      float: left
      .back-icon
        text-align: center
        font-size: .4rem

    .header-input
      flex: 1
      height:.64rem
      background:#fff
      margin-top .12rem
      margin-left .2rem
      border-radius: .1rem
      color:#ccc
      line-height .64rem
      padding-left: .2rem
    .header-right
      min-width: 1.04rem
      padding:0 .1rem
      float: right
      text-align: center
      color:#fff
      .arrow-iocn
        margin-left: -.04rem
        font-size: .24rem
</style>



